@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none !important;
  font-family: "Bebas Neue", cursive;
}

ul {
  list-style: none;
}

.coinlist-image {
  width: 50px;
}

.coinlist-item {
  display: flex;
}

.coinsummary {
  border-color: white !important;
  border-style: outset !important;
}

.coin-data-category {
  font-size: 10px;
}

.delete-icon {
  display: none;
  position: absolute;
  right: 6px;
}

.coin:hover .delete-icon {
  display: block;
}

/* Search styling */

.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: black;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  box-shadow: 10px 5px 5px rgb(105, 105, 105);
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;
}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #f0bf64 0%,
    #f0bf64 48%,
    #f0bf64 100%
  );

  color: black;
}

.coin-input::-webkit-input-placeholder {
  color: black;
}

.coin-input:-ms-input-placeholder {
  color: black;
}

.coin-input::-ms-input-placeholder {
  color: black;
}

.coin-input::placeholder {
  color: black;
}

/* feed */

.tweet-container {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.tweet {
  margin: 10px;
}

.feed-header {
  display: flex;
  font-size: 50px;
  color: #fff;
  justify-content: center;
  font-family: "Bebas Neue", cursive;
}

.Ad-container {
  background-color: black;
  background-image: url("https://i0.wp.com/blockchainappfactory.files.wordpress.com/2021/09/602b81e389dd0.jpg?ssl=1");
  background-size: cover;
  display: flex;
  color: white;
  height: 30vw;
  font-family: "Roboto", sans-serif;
}

.left-container {
  background-color: rgba(134, 134, 134, 0.651);
  width: 30vw;
  border-top-right-radius: 50%;
  border-bottom-right-radius: 50%;
  display: grid;
  align-items: center;
  justify-content: center;
}

.ad-text {
  color: black;
  font-size: 2rem;
}

.ad-button {
  height: 5vw;
  width: 10vw;
  background-color: #fcb956;
  border: none;
  color: black;
  padding: 16px 32px;
  text-align: center;
  transition: 0.3s;
  display: inline-block;
  text-decoration: none;
  cursor: pointer;

  box-shadow: 10px 5px 5px rgb(51, 51, 51);
  font-size: 1.3rem;
}

.ad-button:hover {
  opacity: 1;
  background-color: #806132;
}

.info {
  color: rgb(163, 163, 163);
  margin-left: auto;
  margin-right: auto;
  font-size: 10px;
}

.right-container {
  display: grid;
  width: 30vw;
  background-color: rgba(134, 134, 134, 0.726);
  padding: 50px;
  border-top-left-radius: 50%;
  border-bottom-left-radius: 50%;
}

.ad-data {
  width: 20vw;
  font-size: 1.2rem;
  font-family: "Roboto", sans-serif;
  margin-left: auto;
}

.ad-image {
  height: 50px;
  width: 50px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .Ad-container {
    height: 45vw;
    display: grid;
    background-color: rgba(134, 134, 134, 0.726);
  }
  .right-container {
    display: none;
  }

  .left-container {
    width: 50vw;
    display: grid;
    align-items: center;
    justify-content: center;
    border-top-right-radius: 0%;
    border-bottom-right-radius: 0%;
    margin-left: auto;
    margin-right: auto;
  }

  .ad-text {
    color: black;
    font-size: 2rem;
  }

  .ad-button {
    margin-left: 20%;
    height: 10vw;
    width: 20vw;
    background-color: #fcb956;
    border: none;
    color: black;
    padding: 16px 32px;
    text-align: center;
    transition: 0.3s;
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
    font-size: 1.3rem;
  }
}

@media only screen and (min-device-width: 375px) and (max-device-width: 768px) {
  .Ad-container {
    height: 70vw;
    align-items: center;
  }

  .right-container {
    display: none;
  }

  .left-container {
    width: 50vw;
    height: 40vw;
    display: grid;
    align-items: center;
    justify-content: center;
    border-top-right-radius: 0%;
    border-bottom-right-radius: 0%;
    margin-left: auto;
    margin-right: auto;
  }

  .ad-text {
    color: rgb(247, 178, 76);
    font-size: 1.5rem;
  }

  .ad-button {
    height: 10vw;
    width: 30vw;
    background-color: #fcb956;
    border: none;
    color: black;
    text-align: center;
    transition: 0.3s;
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
    font-size: 1rem;
    margin-left: 30px;
  }

  .ad-button:hover {
    opacity: 1;
    background-color: #806132;
  }

  .info {
    display: none;
  }
}

.home-page {
  min-height: 900px;
  background-color: rgba(0, 0, 0, 0.432);
}

.homePage-container {
  display: flex;
  justify-content: center;
}

.coinList {
  width: 50%;
}

.coin-container {
  display: flex;
  justify-content: center;
}

.coin-row {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 4vw;
  border-bottom: 1px solid #d7d7d7;
  width: 50vw;
}

.coin {
  display: flex;
  align-items: center;
  padding-right: 24px;
  min-width: 15vw;
}

.coin h1 {
  font-size: 26px;
  width: 25vw;
  margin-top: 10px;
}

.coin img {
  height: 30px;
  width: 30px;
  margin-right: 10px;
}

.coin-symbol {
  text-transform: uppercase;
  margin-top: 10px;
  font-size: 20px;
}

.coin-data {
  display: flex;
  text-align: right;
  font-size: 20px;
  justify-content: space-between;
  width: 35vw;
  margin-top: 10px;
}

.coin-price {
  width: 6vw;
}

.coin-volume {
  width: 8vw;
}

.coin-marketcap {
  width: 14vw;
}

.coin-percent {
  width: 5vw;
}

.red {
  color: #f00606;
}

.green {
  color: #11d811;
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) {
  .coin-row {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 6vw;
    border-bottom: 1px solid #d7d7d7;
    width: 80vw;
  }

  .coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 25vw;
  }

  .coin h1 {
    font-size: 20px;
    width: 15vw;
    margin-top: 10px;
  }

  .coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
  }

  .coin-symbol {
    text-transform: uppercase;
    margin-top: 10px;
    font-size: 20px;
  }

  .coin-data {
    display: flex;
    text-align: right;
    font-size: 20px;
    justify-content: space-between;
    width: 55vw;
  }

  .coin-price {
    width: 6vw;
  }

  .coin-volume {
    width: 16vw;
  }

  .coin-marketcap {
    width: 21vw;
  }

  .coin-percent {
    width: 5vw;
  }
}

@media only screen and (min-device-width: 375px) and (max-device-width: 768px) {
  .coin-row {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: auto;
    border-bottom: 1px solid #d7d7d7;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
  }

  .coin {
    display: flex;
    align-items: center;
    padding-right: 0px;
  }

  .coin h1 {
    font-size: 15px;
    width: 25vw;
  }

  .coin img {
    height: 15px;
    width: 15px;
    margin-right: 10px;
  }

  .coin-symbol {
    text-transform: uppercase;
    margin-top: 15px;
    font-size: 10px;
  }

  .coin-data {
    display: flex;
    text-align: right;
    font-size: 15px;
    justify-content: space-between;
    width: 60vw;
    margin-top: 15px;
  }

  .coin-price {
    width: 16vw;
  }

  .coin-volume {
    display: none;
  }

  .coin-marketcap {
    width: 25vw;
  }

  .coin-percent {
    width: 10vw;
  }
}

img {
  height: 40px;
  width: 40px;
  margin-right: 5px;
  margin-top: -5px;
}

.nav-span {
  font-size: 2rem;
  color: rgb(252, 253, 253);
}

.links {
  display: flex;
  margin-right: auto;
}

.chart-container {
  color: #fff;
  width: 600px;
}

.myChart {
  color: #9c9c9c;
}

.button-container {
  display: flex;
  justify-content: center;
}

.time-button {
  border: none;
  background-color: grey;
  color: white;
  padding: 5px 5px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  transition-duration: 0.4s;
}

.time-button:hover {
  background-color: rgb(71, 71, 71);
  color: white;
}

label {
  font-size: 30px;
}

.data-container {
  display: flex;
  font-size: 20px;
}

.coinData-text {
  color: #505050;
}

.coinPage-container {
  padding: 30px;
  display: grid;
  min-height: 800px;
  background-color: rgba(0, 0, 0, 0.432);
  color: white;
}

.coinPage-top {
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}

.coin-image {
  height: 8vh;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}

.chart-container {
  height: 35vh;
  margin-left: auto;
  margin-right: auto;
  margin-top: -20px;
}

.coinData-container {
  width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.crypto-image {
  height: 60px;
  width: 60px;
}

.trending-container {
  padding: 40px;
  height: 900px;
  background-color: rgba(0, 0, 0, 0.432);
}

.trend-rubrik {
  display: flex;
  justify-content: center;
}

